@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap);
body {
    font-family: 'Poppins', sans-serif;
    background: url(https://cdn.discordapp.com/attachments/914446216648097802/1155484111339782174/Yeni_Proje.jpg) no-repeat center center fixed;
    background-size: cover;
    margin: 0;
}

#particles-js {
    position: fixed;
    top: 0;
    width: -webkit-fill-available;
    height: -webkit-fill-available;
}

#root {
    display: flex;
    justify-content: center; /* Yatayda ortala */
    align-items: center; /* Dikeyde ortala */
    height: 100vh;
}

.content {
    width: 1100px;
    height: 560px;
    border-radius: 8px;
    border: 1px solid rgba(255,255,255,.1);
    background-color: #00000070;
    padding: 0 20px;
}

.content p {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);
}

.content h1 {
    text-align: center;
    color: #ffffffd9;
}

.logo {
    display: flex;
    align-items: center;
    grid-gap: 60px;
    gap: 60px;
    margin-top: 40px;
    margin-bottom: 110px;
}

.logo img {
    width: 500px;
}

p a {
    color:#6d0084!important
}

@media only screen and (max-device-width: 640px){    
    #root {
        position: absolute;
        margin-top: 165%;
    }
    .content {
        width: 1100px;
        height: 2400px;
        margin: 0 60px;
        margin-top: 50%;
    }
    .content h1, .content p {
        font-size: 86px;
        margin-top: 34px;
        margin-bottom: 34px;
    }
    .content p {
        font-size: 55px;
        text-align: center;
    }
    .logo {
        margin-top: 125px;
        grid-gap: 30px;
        gap: 30px;
    }
    .logo img {
        width: 530px;
    }
}
